import axios from 'axios';
import { host } from './helpers';

export const fetchLatest = (group, uuid) => {
    let url = `${host}/api/widget/${group}$${uuid}`;

    return axios.get(url)
        .then((response) => {
            return response.data.data;
        }, (error) => {
            return Promise.reject(error);
        });
}


